'use strict';

// MODULES //

import isBuffer from '@stdlib/assert-is-buffer';
import format from '@stdlib/error-tools-fmtprodmsg';
import Buffer from '@stdlib/buffer-ctor';


// MAIN //

/**
* Copies buffer data to a new `Buffer` instance.
*
* @param {Buffer} buffer - buffer from which to copy
* @throws {TypeError} must provide a `Buffer` instance
* @returns {Buffer} new `Buffer` instance
*
* @example
* import fromArray from '@stdlib/buffer-from-array';
*
* var b1 = fromArray( [ 1, 2, 3, 4 ] );
* // returns <Buffer>
*
* var b2 = fromBuffer( b1 );
* // returns <Buffer>
*/
function fromBuffer( buffer ) {
	if ( !isBuffer( buffer ) ) {
		throw new TypeError( format( '0GN3b,Ew', buffer ) );
	}
	return Buffer.from( buffer );
}


// EXPORTS //

export default fromBuffer;
