'use strict';

/**
* Copy buffer data to a new `Buffer` instance.
*
* @module @stdlib/buffer-from-buffer
*
* @example
* import fromArray from '@stdlib/buffer-from-array';
* import copyBuffer from '@stdlib/buffer-from-buffer';
*
* var b1 = fromArray( [ 1, 2, 3, 4 ] );
* // returns <Buffer>
*
* var b2 = copyBuffer( b1 );
* // returns <Buffer>
*/

// MODULES //

import hasFrom from './has_from.js';
import main from './main.js';
import polyfill from './polyfill.js';


// MAIN //

var copyBuffer;
if ( hasFrom ) {
	copyBuffer = main;
} else {
	copyBuffer = polyfill;
}


// EXPORTS //

export default copyBuffer;
